import {style} from "typestyle";

export const commonCss = {
  padding: style({
    padding: '10px'
  }),
  centerText: style({
    textAlign: 'center'
  }),
  noData: style({
    margin: '30px 0'
  }),
  itemLeftMargin: style({
    marginLeft: '6px'
  }),
  topRight: style({
    position: 'absolute',
    top: 0,
    right: 0
  }),
}